
.pageform{
    display: flex;
    flex-direction: column;
    width: 98%%;
    padding: 20px;
    background-color: black;
}
.hemployeeform{
    text-align: center;
    color: white;
}

form {
    margin: 0 auto;
    padding: 20px;
    border: 1px solid #ccc;
    border-radius: 5px;
    background-color: #f9f9f9;
  }
  
  
  input, textarea,select {
    width: 400px;
    padding: 8px;
    margin: 4px 0;
    box-sizing: border-box;
    border: 1px solid #ccc;
    border-radius: 4px;
  }
  
  input:hover , textarea:hover{
    background-color: #f2f2f2;
  }

  textarea {
    resize: vertical;
    height: auto;
  }
  
  button {
    background-color: #4CAF50;
    color: white;
    padding: 10px 20px;
    margin-right: 10px;
    border: none;
    border-radius: 4px;
    
  }
  
  button a {
    color: white;
    text-decoration: none;
  }
  
  button:hover {
    background-color: #45a049;
  }
  
  button:last-child {
    background-color: #f44336;
  }
  
  button:last-child:hover {
    background-color: #da190b;
  }

  .button-container {
    text-align: center;
  }
  

  .toptable {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    gap: 10px;
    margin-top: 20px;

}

.table {
    background-color: #f8f9fa;
    padding: 10px;
    border-radius: 5px;
    font-size: 14px;
}
